<template>
    <div class="outer">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="form-container">
            <el-form-item label="" prop="courseCode">
                <el-input v-model="ruleForm.courseCode"></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseName">
                <el-input v-model="ruleForm.courseName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseCredit">
                <el-input v-model="ruleForm.courseCredit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseClass">
                <el-input v-model="ruleForm.courseClass"></el-input>
            </el-form-item>
            <el-form-item label="" prop="usualWeight" >
                <el-input v-model="ruleForm.usualWeight" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="endWeight" >
                <el-input v-model="ruleForm.endWeight" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseTime">
                <el-input v-model="ruleForm.courseTime" placeholder=": 1-17 1-2"></el-input>
            </el-form-item>
            <el-form-item label="" prop="coursePlace">
                <el-input v-model="ruleForm.coursePlace"></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseType">
                <el-radio-group v-model="ruleForm.courseType">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="courseTermId">
                <el-select v-model="ruleForm.courseTermId" filterable placeholder="">
                    <el-option
                            v-for="item in courseTerm"
                            :key="item.termId"
                            :label="item.termName"
                            :value="item.termId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="teacherId">
                <el-select v-model="ruleForm.teacherId" filterable placeholder="">
                    <el-option
                            v-for="item in teachers"
                            :key="item.userId"
                            :label="item.nickname"
                            :value="item.userId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "add-course",
        data() {
            var timeVerify = (rule, value, callback) => {
                if (value) {
                    var reg = /^\d{1,2}-\d{1,2}[\u4e00-\u9fa5] [\u4e00-\u9fa5]{2}\d{1,2}-\d{1,2}[\u4e00-\u9fa5]$/;
                    if (!reg.test(value)) {
                        callback(new Error(',1-17 1-2'));
                    }
                    callback();
                }
            };
            var endWeightVerify = (rule, value, callback) => {
                if (value != 1 - this.ruleForm.usualWeight) {
                    callback(new Error('1'));
                } else {
                    callback();
                }
            };
            return {
                teachers: [],
                courseTerm: [],
                ruleForm: {
                    courseCode: '',
                    courseName: '',
                    courseCredit: '',
                    courseClass: '',
                    usualWeight: 0.4,
                    endWeight: 0.6,
                    courseTime: '',
                    coursePlace: '',
                    courseType: '',
                    courseTermId: '',
                    teacherId: '',
                    schoolId: this.$store.getters.getUser.schoolId
                },
                rules: {
                    courseCode: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    courseName: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    courseCredit: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    courseClass: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    usualWeight: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    endWeight: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: endWeightVerify, trigger: 'blur'}
                    ],
                    courseTime: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: timeVerify, trigger: 'blur'}
                    ],
                    coursePlace: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    courseTermId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    teacherId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    courseType: [
                        {required: true, message: '', trigger: 'change'}
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$axios.post("/course/add", this.ruleForm,
                            {
                                headers: {
                                    "Authorization": this.$store.getters.getToken
                                }
                            }).then(res => {
                            this.$message({
                                message: res.data.message,
                                type: "success"
                            });
                            this.resetForm('ruleForm');
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            getList() {
                const _this = this;
                this.$axios.get("/user/getTeachers?schoolId=" + _this.$store.getters.getUser.schoolId,
                    {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }
                ).then(response => {
                    var data = response.data.data;
                    _this.teachers = data;
                })
            },
            getTermList() {
                const _this = this;
                this.$axios.get("/term/getAll?notAll=1",
                    {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }
                ).then(response => {
                    var data = response.data.data;
                    _this.courseTerm = data.list;
                    _this.ruleForm.courseTermId = data.this;
                })
            },
        },

        created() {
            this.getList();
            this.getTermList();
        }

    }
</script>

<style scoped>

    .outer {

        background: #E9EEF3;
    }

    .form-container {
        width: 400px;
        margin: 0 auto;
        text-align: center;
    }
</style>