<template>
    <div>
        <div id="print">
            <h3 align="center">
                :{{course.courseName}}[:{{course.courseCode}}]
            </h3>
            <h4 align="center">
                :{{course.courseClass}}
                :{{course.courseCredit}}({{course.courseType}})
                :{{teacher.nickname}}
            </h4>
            <el-table
                    class="table"
                    :data="studentList"
                    border
                    stripe>

                <el-table-column
                        width="50px"
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        {{scope.$index+1}}
                    </template>
                </el-table-column>

                <el-table-column
                        width="100px"
                        prop="student.username"
                        label=""
                        align="center">
                </el-table-column>

                <el-table-column
                        width="100px"
                        prop="student.nickname"
                        label=""
                        align="center">
                </el-table-column>

                <el-table-column
                        width="50px"
                        prop="student.sex"
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        {{scope.$index+1}}
                    </template>
                </el-table-column>
                <el-table-column
                        width="150px"
                        prop="student.studentClass"
                        label=""
                        align="center">
                </el-table-column>
                <el-table-column
                        width="100px"
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span v-if="!showEdit[scope.$index]">{{scope.row.grade.usualGrade}}</span>
                        <el-input type="text" v-model="scope.row.grade.usualGrade" v-else placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                        width="100px"
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span v-if="!showEdit[scope.$index]">{{scope.row.grade.endGrade}}</span>
                        <el-input type="text" v-model="scope.row.grade.endGrade" v-else placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                        width="100px"
                        prop="grade.totalGrade"
                        label=""
                        align="center">
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label=""
                        align="center"
                        width="100">
                    <template slot-scope="scope">
                        <el-button v-if="!showEdit[scope.$index]" @click="showUpdate(scope.$index,scope.row)"
                                   type="text">
                            <!--                        <i class="el-icon-edit"></i>-->
                            
                        </el-button>
                        <el-button v-if="showEdit[scope.$index]" @click="submitUpdate(scope.$index,scope.row)"
                                   type="primary" round>
                            
                        </el-button>
                        <br>
                        <el-button v-if="showEdit[scope.$index]" @click="cancelUpdate(scope.$index)"
                                   type="primary" round>
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

        </div>
        <div style="margin-top: 50px; text-align: center">
            <button v-print="'#print'"></button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "add-grade",
        data() {
            return {
                showEdit: [],
                studentList: [],
                course: '',
                teacher: '',
                date: '',
            }
        },
        methods: {
            getList() {
                const _this = this;
                this.$axios.get("/grade/showStudentsCourseGrade?courseId=" + _this.$store.getters.getCourseId,
                    {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }
                ).then(response => {
                    var data = response.data.data;
                    _this.studentList = data.list;
                    _this.course = data.course;
                    _this.teacher = data.teacher;
                })
            },
            getFormatDate() {
                var date = new Date();
                var month = date.getMonth() + 1;
                var strDate = date.getDate();
                if (month >= 1 && month <= 9) {
                    month = "0" + month;
                }
                if (strDate >= 0 && strDate <= 9) {
                    strDate = "0" + strDate;
                }
                var currentDate = date.getFullYear() + "" + month + "" + strDate
                    + "" + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
                this.date = currentDate;
            },
            showUpdate(index) {
                this.showEdit[index] = true;
                this.$set(this.showEdit, index, true); //$set
            },
            //
            cancelUpdate(index) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.$set(this.showEdit, index, false);
                        this.getList();
                        this.getTermList();
                        this.getTeacherList();
                    })
                    .catch(() => {
                    });
            },
            submitUpdate(index,row) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.$axios.post("/grade/update", row.grade,
                            {
                                headers: {
                                    "Authorization": this.$store.getters.getToken
                                }
                            }).then(res => {
                            this.$message({
                                message:res.data.message,
                                type:"success"
                            })
                            this.showEdit[index] = false;
                            this.$set(this.showEdit, index, false);
                            this.getList();
                        })
                    })
                    .catch(() => {
                    });
            },
        },
        created() {
            this.getList();
            this.getFormatDate();
        }
    }
</script>

<style lang="less" scoped>

    .table {
        max-width: 860px;
        margin: auto;
        border: 1px solid black;
    }


    .top {
        margin: 0 auto;
        text-align: center;
    }

    .page {
        margin-top: 15px;
        text-align: center;
    }
</style>